@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
html,body,#root,.App{width:100%;height:100%;margin:0;padding:0}.App{font-family:"Montserrat", sans-serif;text-align:center;display:flex}.App svg{height:100%}.spacer{height:100%}.bubble-wrap{margin:0 auto;display:flex;flex-direction:column}.colors-wrap{height:100%;width:300px;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23)}.colors-wrap ul{list-style:none;text-align:start}.colors-wrap ul li{cursor:pointer;padding:6px 0;display:flex;justify-content:flex-start;width:100%}.colors-wrap ul li a{display:flex}.delete{color:#d85757;margin-right:16px}.color-box{height:16px;width:16px;border:1px lightgray solid;margin-right:36px}form{margin-top:60px;display:flex;flex-direction:column}form legend{padding-left:40px;font-weight:bold;text-align:left;text-decoration:underline;margin-bottom:16px}form label{display:flex;justify-content:space-between;padding:4px 36px 4px 40px}form input{width:100px}.button-row{display:flex;justify-content:space-between;padding:0 36px 0 41px}.button-row button{border:none;background-color:black;color:white;padding:3px 8px;margin-top:16px}.color-list--name{margin-right:8px}.error{color:red;text-align:left}

